.heading {
  color: $pink;
  font-family: Montserrat;
  font-size: 3.125rem;
  font-weight: 900;
  line-height: 3.8125rem;
  letter-spacing: 0%;
  align-items: center;
  margin: 0 auto;
  font-style: normal;
  text-align: center;
  position: absolute;
  top: 40px;
  transform: translateX(50%);
  width: 600px;
}

.headerLogo {
  width: 9rem;
  margin-bottom: 13px;
}

header {
  background: url('../img/topBg.png');
  height: 27.875rem;
  background-repeat: no-repeat;
  background-size: cover;
  height: 20rem;
}

.inputBox {
  display: flex;
  align-items: center;
  text-align: center;
  margin: 0 auto;
}

#countriesList {
  background-color: transparent;
  border: 0;
  color: #fff;
}

.p23 {
  color: #fff;
  font-family: Montserrat;
}

::placeholder {
  color: rgb(255, 255, 255);
  font-family: Montserrat;
}

.inpBoxItem {
  display: flex;
  align-items: center;
}

.inputBox {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.second-input {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.second-input, .inpBoxItem {
  border-radius: 1.25rem;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  border: 0;
  padding: 10px;
  width: 270px;
  height: 40px;
  display: inline;
  border-radius: 20px;
}

.secondInputBtn {
  background: none;
  border: none;
  &:hover {
    background: none;
    border: none;
  }
  &:focus {
    background: none;
    border: none;
    box-shadow: none;
  }
}

.dropdown-bootstrap-li {
  background-color: rgba(255, 255, 255, 0.7);
  color: black;
  transition: background-color 0.3s;
  &:hover {
    background-color: rgb(189, 189, 189);
    color: black;
  }
  &:first-child {
    border-radius: 25px 25px 0 0;
  }
  &:last-child {
    border-radius: 0 0 25px 25px;
  }
  button {
    background-color: transparent;
    border: none;
    color: black;
    &:hover {
      background-color: transparent;
      color: black;
    }
  }
}

.dropdown-menu {
  height: 200px;
  overflow: auto;
  border-radius: 25px;
  scrollbar-width: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    display: none;
  }
}

.inputList {
  align-content: center;
  align-items: center;
  text-align: center;
  margin: 0 auto;
  margin-top: 40px;
}

.heading {
  align-items: center;
  text-align: center;
}

.noEvents {
  color: white;
}

.second-input {
  min-width: 200px;
  display: inline-block;
  align-items: center;
  padding: -20px;
}

.secondInputBtn {
  margin: 0;
  padding: 0;
}

.inputList {
  display: flex;
  justify-content: center;
  .second-input {
    margin-left: 20px;
  }
}
